.section {
	background-color: #EEE4DA;
	padding: 50px 0;
}

.section__caption {
	position: relative;
	margin: 0 0 50px 0;

	&--catalogue {
		@media (max-width: 500px) {
			display: none;
		}
	}

	&-text {
		position: relative;
		display: inline-block;
		padding: 0 30px;
		margin: 0;
		color: #818181;
		background-color: #EEE4DA;
		text-align: center;
		font-family: Cormorant SC;
		font-size: 96px;
		font-style: normal;
		font-weight: 700;
		line-height: 120%; /* 115.2px */
		letter-spacing: -2.88px;
		z-index: 2;
	}

	&::before {
		@include pseudo;
		top: 50%;
		left: 0;
		height: 2px;
		width: 100%;
		background-color: #818181;
		z-index: 1;
	}
}