.catalogue {
}

.catalogue__list {
	display: grid;
	justify-content: center;
	align-content: center;
	grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
	gap: 2rem;
}

.catalogue__item {
	width: 310px;
	height: 423px;
	border: 10px solid #efd3b8ab;
	border-radius: 6px;
	overflow: hidden;
	margin: 0 auto;
}

.catalogue__caption {
	position: absolute;
	bottom: 10px;
	padding-left: 10px;
	margin: 0;
	color: #383838;
	font-family: Cormorant SC;
	font-size: 48px;
	text-align: left;
	font-weight: 700;
	line-height: 120%; /* 57.6px */
	letter-spacing: -1.44px;
	mix-blend-mode: color-burn;

	&--light {
		mix-blend-mode: normal;
		color: #EEE4DA;
	}
}

.catalogue__link {
	position: relative;
	width: 100%;
	height: 100%;
}